<template>
    <div class="getinfo">
        <table>
            <tr>请阅读格式</tr>
            <tr v-for="item in selects">
                <td>item</td>
                <td><input type="text" ref="text"></td>
            </tr>
        </table>
        <button @click="getinfo">提交</button>
    </div>
</template>
<script>
import {user} from 'data/headblog'
export default {
    name: 'headgetinfo',
    data(){
        return {
            person:{},
            selects:['姓名','年龄','个人简介']
        }
    },
    created(){
        /* this.person = new headblog() */
    },
    methods:{
        getinfo(){
            let info = this.$refs.text
            this.person = new user(info[0].value,info[1].value,info[2].value)
        }

    }
}
</script>
<style>

</style>


